import React from "react";
import { Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Var from '../variable';
import '../styles/main.css'

export default function About({ darkState }) {
  const useStyles = makeStyles((theme) => ({
    title: {
      color: darkState ? "#ffffff" : '#343a40',
    }
  }));
  const classes = useStyles();
  return (
    <div className="">
      <div className={[classes.title, "text-6xl font-bold hp"].join(' ')}>{Var.name} <span className="text-primary">{Var.surname}</span></div>
      <div className="sub-heading mb-8">{Var.state} · {Var.city} · <a title="email" href="mailto:name@email.com">{Var.email}</a></div>
      <Typography paragraph>
        { Var.about.map((el,i) => <React.Fragment key={i}>{el}{i < (Var.about.length - 1) ? <br /> : ''}</React.Fragment>) }
      </Typography>
    </div>
  );
}
