import React from "react";
import { Typography, Card, CardContent } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import DataTable from "react-data-table-component";
import "../styles/main.css";

export default function Github({ darkState, report }) {
  const useStyles = makeStyles((theme) => ({
    title: {
      color: darkState ? "#ffffff" : "#343a40"
    }
  }));
  const classes = useStyles();

  const theme = darkState ? 'dark' : 'default';

  const columns = [
    {
      name: "Name",
      selector: "name",
      sortable: true,
      cell: row => <a href={row.html_url}>{row.name}</a>
    },
    {
      name: "Language",
      selector: "language",
      sortable: true
    },
    {
      name: "Stargazers",
      selector: "stargazers_count",
      sortable: true,
      maxWidth: "100px"
    },
    {
      name: "Forks",
      selector: "forks",
      sortable: true,
      maxWidth: "100px"
    },
    {
      name: "Created",
      selector: "created_at",
      sortable: true,
      cell: ({ created_at }) => new Date(created_at).toLocaleString()
    },
    {
      name: "Pushed",
      selector: "pushed_at",
      sortable: true,
      cell: ({ pushed_at }) => new Date(pushed_at).toLocaleString()
    },
    {
      name: "Description",
      selector: "description",
      sortable: true,
      grow: 3,
      wrap: true
    }
  ];
  return (
    <div className="">
      <div className={[classes.title, "text-5xl font-bold mb-8"].join(" ")}>
        Github Project ({report.length})
      </div>
      <Card className="w-full mx-auto m-16 md:m-0">
        <CardContent className="flex flex-col items-center justify-center p-0 py-20">
          <DataTable
            columns={columns}
            data={report}
            theme={theme}
            defaultSortField="stargazers_count"
            defaultSortAsc={false}
            highlightOnHover
            noHeader
            pagination
            noDataComponent={<div style={{ padding: 24 }}>No element</div>}
          />
        </CardContent>
      </Card>
    </div>
  );
}
