import React from "react";
import PropTypes from "prop-types";
import {
  AppBar,
  CssBaseline,
  Drawer,
  Hidden,
  IconButton,
  List,
  ListItem,
  ListItemIcon,
  ListItemText,
  Toolbar,
  //Typography,
} from "@material-ui/core";
import {
  Info,
  Menu,
  Work,
  School,
  Grade,
  Favorite,
  GitHub
} from "@material-ui/icons";
import { makeStyles, useTheme } from "@material-ui/core/styles";
import { About, Experiences, Education, Skills, Interests, Github } from "./page";
import "./styles/main.css";
import "./App.css";
import DarkModeToggle from "react-dark-mode-toggle";

import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import {
  blue,
  grey,
  deepPurple,
  deepOrange
} from "@material-ui/core/colors";

const drawerWidth = 240;

function App(props) {
  const { windows } = props;
  const container =
    windows !== undefined ? () => windows().document.body : undefined;
  const prefersDark = window.matchMedia("(prefers-color-scheme: dark)").matches;
  const [state, setState] = React.useState({
    mobileOpen: false,
    page: 3,
    darkState: prefersDark,
    repo: []
  });
  const { mobileOpen, page, darkState, repo } = state;

  const useStyles = makeStyles((theme) => ({
    root: {
      display: "flex"
    },
    drawer: {
      [theme.breakpoints.up("sm")]: {
        width: drawerWidth,
        flexShrink: 0
      }
    },
    appBar: {
      [theme.breakpoints.up("sm")]: {
        width: `calc(100% - ${drawerWidth}px)`,
        marginLeft: drawerWidth
      }
    },
    menuButton: {
      marginRight: theme.spacing(2),
      [theme.breakpoints.up("sm")]: {
        display: "none"
      },
      backgroundColor: darkState ? grey[900] : blue[500]
    },
    // necessary for content to be below app bar
    toolbar: theme.mixins.toolbar,
    drawerPaper: {
      width: drawerWidth,
      color: "#ffffff",
      backgroundColor: darkState ? grey[900] : blue[500]
    },
    content: {
      flexGrow: 1,
      padding: theme.spacing(3)
    }
  }));

  const palletType = darkState ? "dark" : "light";
  const mainPrimaryColor = darkState ? grey[900] : blue[500];
  const mainSecondaryColor = darkState ? deepOrange[900] : deepPurple[500];
  const darkTheme = createMuiTheme({
    palette: {
      type: palletType,
      primary: {
        main: mainPrimaryColor
      },
      secondary: {
        main: mainSecondaryColor
      }
    }
  });

  const classes = useStyles();
  const theme = useTheme();
  const handleDrawerToggle = () =>
    setState((prev) => ({ ...prev, mobileOpen: !mobileOpen }));
  const changePage = (page) => setState((prev) => ({ ...prev, page }));
  const handleThemeChange = () =>
    setState((prev) => ({ ...prev, darkState: !darkState }));

  const menu = [
    { title: "About", icon: <Info /> },
    { title: "Experiences", icon: <Work /> },
    { title: "Education", icon: <School /> },
    { title: "Skills", icon: <Grade /> },
    { title: "Interests", icon: <Favorite /> },
    { title: `Github Project (${repo.length})`, icon: <GitHub /> }
  ];

  React.useEffect(() => {
    fetch('https://api.github.com/users/gaetanozappi/repos?per_page=1000')
          .then((response) => response.json())
          .then((data) => {
            console.log(data)
            setState((prev) => ({ ...prev, repo: [...prev.repo,...data] }));
          })
          .catch((e) => {
            console.error("Error", e);
          });
          fetch('https://api.github.com/users/TheCaponeBrothers/repos?per_page=1000')
                .then((response) => response.json())
                .then((data) => {
                  console.log(data)
                  setState((prev) => ({ ...prev, repo: [...prev.repo,...data] }));
                })
                .catch((e) => {
                  console.error("Error", e);
                });
  }, []);

  const drawer = (
    <div className="mt-32">
      <div className={classes.toolbar} />
      <List>
        {menu.map(({ title, icon }, index) => (
          <ListItem button key={title} onClick={() => changePage(index)}>
            <ListItemIcon
              style={{ color: page === index ? "#ffffff" : "#ffffff80" }}
            >
              {icon}
            </ListItemIcon>
            <ListItemText
              primary={<span className="font-bold">{title}</span>}
              style={{ color: page === index ? "#ffffff" : "#ffffff80" }}
            />
          </ListItem>
        ))}
      </List>
    </div>
  );

  return (
    <ThemeProvider theme={darkTheme}>
      <div className={classes.root}>
        <CssBaseline />
        <AppBar position="fixed" className={classes.appBar} style={{ backgroundColor: "transparent", boxShadow: "none" }}>
          <Toolbar className={"shadow-none"}>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              className={classes.menuButton}
            >
              <Menu />
            </IconButton>
            <div className="flex-1 text-right" style={{ writingMode: "vertical-rl" }}>
              <DarkModeToggle
                onChange={handleThemeChange}
                checked={darkState}
                size={60}
                //style={{ display: "block" }}
              />
            </div>
          </Toolbar>
        </AppBar>

        <nav className={classes.drawer} aria-label="mailbox folders">
          {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
          <Hidden smUp implementation="css">
            <Drawer
              container={container}
              variant="temporary"
              anchor={theme.direction === "rtl" ? "right" : "left"}
              open={mobileOpen}
              onClose={handleDrawerToggle}
              classes={{
                paper: classes.drawerPaper
              }}
              ModalProps={{
                keepMounted: true // Better open performance on mobile.
              }}
            >
              {drawer}
            </Drawer>
          </Hidden>
          <Hidden xsDown implementation="css">
            <Drawer
              classes={{
                paper: classes.drawerPaper
              }}
              variant="permanent"
              open
            >
              {drawer}
            </Drawer>
          </Hidden>
        </nav>
        <main className={classes.content}>
          <div className={classes.toolbar} />
          {page === 0 && <About darkState={darkState} />}
          {page === 1 && <Experiences darkState={darkState} />}
          {page === 2 && <Education darkState={darkState} />}
          {page === 3 && <Skills darkState={darkState} />}
          {page === 4 && <Interests darkState={darkState} />}
          {page === 5 && <Github darkState={darkState} report={repo} />}
        </main>
      </div>
    </ThemeProvider>
  );
}

App.propTypes = {
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  windows: PropTypes.func
};

export default App;
